
@import '../../../styles/mixins.scss';

.s-input-select-container {
  display: flex;
  flex-direction: column;
  margin: s-space(base);
  position: relative;

  .s-input-label {
    color: s-color(neutral, 700);
    margin-bottom: s-space(extra-tight);
    text-align: left;
  }

  .s-input-field {
    box-sizing: border-box;
    font-family: $s-theme-font-family;
    border-radius: 3px;
    border: 1px s-color(neutral, 300) solid;
    height: 40px;
    padding: s-space(extra-tight) s-space(tight);
    -webkit-appearance: none;
    -moz-appearance: none;
    cursor: pointer;


    &:focus {
      outline: s-color(primary, 400) 1px solid;
    }

    &::placeholder {
      color: s-color(neutral, 300);
    }

    &::-ms-expand {
      display: none;
    }
  }

  .s-select-chevron-down {
    position: absolute;
    color: s-color(primary, 400);
    right: s-space(tight);
    top: 35px;
    pointer-events: none;
  }
}



