@import '../../../styles/mixins.scss';

.s-text-area-container {
  display: flex;
  flex-direction: column;
  margin: s-space(base);

  .s-text-area-label {
    color: s-color(neutral, 600);
    margin-bottom: s-space(extra-tight);
  }

  .s-text-area-field {
    font-family: $s-theme-font-family;
    border-radius: 3px;
    border: 1px s-color(neutral, 300) solid;
    padding: s-space(tight) s-space(tight);

    &:focus {
      outline: s-color(primary, 400) 1px solid;
    }

    &::placeholder {
      color: s-color(neutral, 300);
    }
  }
}