@import '../../../styles/mixins.scss';

.dropdown-select{
  border: 2px solid s-color(primary, 300);

  padding:5px;
  background: #fff;
  height: s-space(extra-loose);
  min-width: s-space(extra-loose);
  select{
    width:100%;
    height:100%;
    border: none;
    color: #000;
    background: transparent;
    background: inherit;
    box-shadow:none;
    font-weight: 600;
    &:focus {
      outline: none;
      box-shadow:none;
    }
    font-size: 16px;
  }
}
.select-active{
  background-image: linear-gradient(60deg,#2597d0,#5bbddc);
  select{
    color:#fff;
  }
}

.round{
  border-radius: $s-theme-base-border-radius;
}

.square{
  border-radius: 5px;
}
.dropdown-small {
  min-width: 144px;
  max-width: 179px;
}

.dropdown-medium {
  max-width: 264px;
  min-width: 180px;
}

.dropdown-fluid {
  width: 100%;
}