body.menu-open {
  overflow-y: hidden;
  overflow-x: hidden; }
  body.menu-open .bm-menu {
    padding-left: 2rem; }
  body.menu-open .bm-burger-button {
    transform: rotate(90deg); }

.omart-logo {
  max-width: calc(100% - 16px);
  margin-top: 3rem; }

.sidebar-logo .simply-logo {
  margin-top: 2rem;
  padding-bottom: 0px;
  height: 47px; }
  @media (max-width: 768px) {
    .sidebar-logo .simply-logo {
      padding-top: 0px;
      padding-bottom: 0px; } }

.bm-menu h6 {
  padding-top: 1rem; }

/* Individual item */
.bm-item {
  display: inline-block;
  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #2F3946;
  transition: color 0.2s;
  font-size: 16px; }

a.bm-item {
  color: #697489; }

/* Change color on hover */
a.bm-item:hover {
  color: #1287BF; }

/* The rest copied directly from react-burger-menu docs */
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 1.5rem;
  top: 26px;
  z-index: 3 !important;
  transition-delay: 0s;
  transition: all 0.25s ease-in-out; }
  .bm-burger-button:hover .bm-burger-bars {
    background-color: #0097D4;
    opacity: 1 !important; }
  @media (max-width: 768px) {
    .bm-burger-button {
      top: 10px;
      left: 11px; } }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background-color: #2597d0;
  transition: all 0.25s ease-in-out; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 50px;
  width: 50px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #59ACF3 !important;
  height: 20px; }

/* General sidebar styles */
.bm-menu {
  background: white;
  padding: 1.5rem 2rem;
  font-size: 1.15em; }
  @media (max-width: 768px) {
    .bm-menu {
      padding: 0px 1rem 1rem 1rem; } }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: white; }

/* Wrapper for item list */
.bm-item-list {
  color: #2F3946;
  text-align: left; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.4) !important; }
