@import '../../../styles/mixins.scss';

$s-btn-border-radius: $s-theme-base-border-radius;

// Primary Button
// $s-btn-primary-background-color: s-color(primary, 400);
$s-btn-primary-background-color: s-color(primary, 500);
$s-btn-primary-background-color-end: s-color(primary, 400);
$s-btn-primary-text-color-dark: s-color(neutral, 100);
$s-btn-primary-text-color-light: s-color(neutral, 100);
$s-btn-primary-background-color--hover: s-color(primary, 500);
$s-btn-primary-background-color--active: s-color(primary, 500);

// Secondary Button
// $s-btn-secondary-background-color: s-color(secondary, 400);
$s-btn-secondary-background-color: s-color(secondary, 500);
$s-btn-secondary-background-color-end: s-color(secondary, 300);
$s-btn-secondary-text-color-dark: s-color(neutral, 100);
$s-btn-secondary-text-color-light: s-color(neutral, 000);
$s-btn-secondary-background-color--hover: s-color(secondary, 400);
$s-btn-secondary-background-color--active: s-color(secondary, 400);

// Tertiary Button
// $s-btn-tertiary-background-color: s-color(tertiary, 400);
$s-btn-tertiary-background-color: s-color(tertiary, 500);
$s-btn-tertiary-background-color-end: s-color(tertiary, 300);
$s-btn-tertiary-text-color-dark: s-color(neutral, 100);
$s-btn-tertiary-text-color-light: s-color(neutral, 000);
$s-btn-tertiary-background-color--hover: s-color(tertiary, 400);
$s-btn-tertiary-background-color--active: s-color(tertiary, 400);

// Success Button
$s-btn-success-background-color: s-color(success, 400);
$s-btn-success-text-color-dark: s-color(success, 600);
$s-btn-success-text-color-light: s-color(neutral, 000);
$s-btn-success-background-color--hover: s-color(success, 300);
$s-btn-success-background-color--active: s-color(success, 500);

// Info Button
$s-btn-info-background-color: s-color(info, 400);
$s-btn-info-text-color-dark: s-color(info, 600);
$s-btn-info-text-color-light: s-color(neutral, 000);
$s-btn-info-background-color--hover: s-color(info, 300);
$s-btn-info-background-color--active: s-color(info, 500);

// Danger Button
$s-btn-danger-background-color: s-color(danger, 400);
$s-btn-danger-text-color-dark: s-color(danger, 700);
$s-btn-danger-text-color-light: s-color(neutral, 000);
$s-btn-danger-background-color--hover: s-color(danger, 300);
$s-btn-danger-background-color--active: s-color(danger, 500);

// Ghost Button
$s-btn-ghost-background-color: transparent;
$s-btn-ghost-text-color: s-color(primary, 300);
$s-btn-ghost-text-color-dark: s-color(primary, 000);
$s-btn-ghost-text-color-light: s-color(neutral, 000);
$s-btn-ghost-background-color--hover: s-color(primary, 300);
$s-btn-ghost-background-color--active: s-color(primary, 400);

// Ghost Button
$s-btn-filled-background-color: #FFF;
$s-btn-filled-text-color: s-color(primary, 300);
$s-btn-filled-text-color-dark: s-color(primary, 000);
$s-btn-filled-text-color-light: s-color(neutral, 000);
$s-btn-filled-background-color--hover: s-color(primary, 300);
$s-btn-filled-background-color--active: s-color(primary, 400);

button {
  transition: all 0.25s ease-in-out !important;
}

.s-btn {
  height: s-space(extra-loose);
  min-width: s-space(extra-loose);
  // transition: all s-easing(base) s-duration(base);
  border-radius: $s-btn-border-radius;
  border: none;
  box-sizing: border-box;
  padding: 0 s-space(base);
  outline: 1px solid transparent;
  background-size: auto 200%;
  background-position: 0 100%;


  &:hover {
    background-position: 0 0;
    box-shadow: s-depth(100);
  }
 }

.s-btn-primary {
  color: s-color-contrast($s-btn-primary-background-color, $s-btn-primary-text-color-dark, $s-btn-primary-text-color-light);
  background-image: linear-gradient(45deg, $s-btn-primary-background-color 0%, $s-btn-primary-background-color-end 100%);

  &:hover,
  &:focus {
    // background: $s-btn-primary-background-color--hover;
    background-image: linear-gradient(60deg, $s-btn-primary-background-color 0%, $s-btn-primary-background-color-end 100%);
    color: s-color(neutral, 100);
  }

  &:active {
    background: $s-btn-primary-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-secondary {
  color: s-color-contrast($s-btn-secondary-background-color, $s-btn-secondary-text-color-dark, $s-btn-secondary-text-color-light);
  background-image: linear-gradient(45deg, $s-btn-secondary-background-color 0%, $s-btn-secondary-background-color-end 100%);

  &:hover,
  &:focus {
    background-image: linear-gradient(60deg, $s-btn-secondary-background-color 0%, $s-btn-secondary-background-color-end 100%);
  }

  &:active {
    background: $s-btn-secondary-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-tertiary {
  // background: $s-btn-tertiary-background-color;
  color: s-color-contrast($s-btn-tertiary-background-color, $s-btn-tertiary-text-color-dark, $s-btn-tertiary-text-color-light);
  background-image: linear-gradient(45deg, $s-btn-tertiary-background-color 0%, $s-btn-tertiary-background-color-end 100%);
  &:hover,
  &:focus {
    background-image: linear-gradient(60deg, $s-btn-tertiary-background-color 0%, $s-btn-tertiary-background-color-end 100%);
  }

  &:active {
    background: $s-btn-tertiary-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-success {
  background: $s-btn-success-background-color;
  color: s-color-contrast($s-btn-success-background-color, $s-btn-success-text-color-dark, $s-btn-success-text-color-light);

  &:hover,
  &:focus {
    background: $s-btn-success-background-color--hover;
  }

  &:active {
    background: $s-btn-success-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-info {
  background: $s-btn-info-background-color;
  color: s-color-contrast($s-btn-info-background-color, $s-btn-info-text-color-dark, $s-btn-info-text-color-light);

  &:hover,
  &:focus {
    background: $s-btn-info-background-color--hover;
  }

  &:active {
    background: $s-btn-info-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-danger {
  background: $s-btn-danger-background-color;
  color: s-color-contrast($s-btn-danger-background-color, $s-btn-danger-text-color-dark, $s-btn-danger-text-color-light);

  &:hover,
  &:focus {
    background: $s-btn-danger-background-color--hover;
  }

  &:active {
    background: $s-btn-danger-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-btn-ghost {
  background: $s-btn-ghost-background-color;
  color: $s-btn-ghost-text-color;
  border: 2px solid s-color(primary, 300);

  &:hover,
  &:focus {
    background: s-color(primary, 500);
    color: s-color(neutral, 100);
    border: 2px solid s-color(primary, 500);
    box-shadow: s-depth(100);
  }

  &:active {
    background: $s-btn-ghost-background-color--active;
    color: s-color-contrast($s-btn-ghost-background-color--active, $s-btn-ghost-text-color-dark, $s-btn-ghost-text-color-light);
    box-shadow: s-depth(000);
  }
}

.s-btn-filled {
  background: $s-btn-filled-background-color;
  color: $s-btn-filled-text-color;
  border: 2px solid s-color(primary, 300);

  &:hover,
  &:focus {
    background: s-color(primary, 500);
    color: s-color(neutral, 100);
    border: 2px solid s-color(primary, 500);
    box-shadow: s-depth(100);
  }

  &:active {
    background: $s-btn-filled-background-color--active;
    color: s-color-contrast($s-btn-filled-background-color--active, $s-btn-filled-text-color-dark, $s-btn-filled-text-color-light);
    box-shadow: s-depth(000);
  }
}

.s-btn-small {
  min-width: 144px;
  max-width: 179px;
}

.s-btn-medium {
  max-width: 264px;
  min-width: 180px;
}

.s-btn-fluid {
  width: 100%;
}

.s-btn-disabled{
  background: darkgrey;
  &:hover{
    background: darkgrey;
  }
}
