@import '../../../styles/mixins.scss';

// Primary Button
$s-btn-circle-primary-background-color: s-color(primary, 400);
$s-btn-circle-primary-text-color-dark: s-color(primary, 700);
$s-btn-circle-primary-text-color-light: s-color(neutral, 000);
$s-btn-circle-primary-background-color--hover: s-color(primary, 300);
$s-btn-circle-primary-background-color--active: s-color(primary, 500);

// Secondary Button
$s-btn-circle-secondary-background-color: s-color(secondary, 400);
$s-btn-circle-secondary-text-color-dark: s-color(secondary, 700);
$s-btn-circle-secondary-text-color-light: s-color(neutral, 000);
$s-btn-circle-secondary-background-color--hover: s-color(secondary, 300);
$s-btn-circle-secondary-background-color--active: s-color(secondary, 500);

// Success Button
$s-btn-circle-success-background-color: s-color(success, 400);
$s-btn-circle-success-text-color-dark: s-color(success, 600);
$s-btn-circle-success-text-color-light: s-color(neutral, 000);
$s-btn-circle-success-background-color--hover: s-color(success, 300);
$s-btn-circle-success-background-color--active: s-color(success, 500);

// Info Button
$s-btn-circle-info-background-color: s-color(info, 400);
$s-btn-circle-info-text-color-dark: s-color(info, 600);
$s-btn-circle-info-text-color-light: s-color(neutral, 000);
$s-btn-circle-info-background-color--hover: s-color(info, 300);
$s-btn-circle-info-background-color--active: s-color(info, 500);

// Danger Button
$s-btn-circle-danger-background-color: s-color(danger, 400);
$s-btn-circle-danger-text-color-dark: s-color(danger, 700);
$s-btn-circle-danger-text-color-light: s-color(neutral, 000);
$s-btn-circle-danger-background-color--hover: s-color(danger, 300);
$s-btn-circle-danger-background-color--active: s-color(danger, 500);

// Ghost Button
$s-btn-circle-ghost-background-color: transparent;
$s-btn-circle-ghost-text-color: s-color(primary, 400);
$s-btn-circle-ghost-text-color-dark: s-color(primary, 600);
$s-btn-circle-ghost-text-color-light: s-color(neutral, 000);
$s-btn-circle-ghost-background-color--hover: s-color(primary, 400);
$s-btn-circle-ghost-background-color--active: s-color(primary, 500);

// Filled Button
$s-btn-circle-filled-background-color: #FFF;
$s-btn-circle-filled-text-color: s-color(primary, 400);
$s-btn-circle-filled-text-color-dark: s-color(primary, 600);
$s-btn-circle-filled-text-color-light: s-color(neutral, 000);
$s-btn-circle-filled-background-color--hover: s-color(primary, 400);
$s-btn-circle-filled-background-color--active: s-color(primary, 500);


.s-circle-btn {
  height: 48px;
  width: 48px;
  border-radius: 50%;
  transition: all s-easing(base) s-duration(base);
  transition: box-shadow s-easing(base) s-duration(fast);
  border: none;
  box-sizing: border-box;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    box-shadow: s-depth(100);
  }
 }

.s-circle-btn-primary {
  background: $s-btn-circle-primary-background-color;
  color: s-color-contrast($s-btn-circle-primary-background-color, $s-btn-circle-primary-text-color-dark, $s-btn-circle-primary-text-color-light);

  &:hover,
  &:focus {
    background: $s-btn-circle-primary-background-color--hover;
  }

  &:active {
    background: $s-btn-circle-primary-background-color--active;
    box-shadow: s-depth(000);
  }
}

.s-circle-btn-secondary {
  background: $s-btn-circle-secondary-background-color;
