@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,700');

:root {
  font-family: $s-theme-font-family;
  font-size: $s-theme-base-font-size;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

body {
  margin: 0;
  padding: 0;
}

/////////////////////////////////////////////////////////////////////////////
/// Utility Classes
/////////////////////////////////////////////////////////////////////////////

$app-background-color:    s-color(neutral, 100);
$s-base-text-color:      s-color(neutral, 700);
$s-brand-text-color:     s-color(primary, 400);
$s-secondary-text-color: s-color(neutral, 500);
$s-base-link-text-color: $s-brand-text-color;

html {
  background-color: $app-background-color;
  color: $s-base-text-color;
}

// Typography
.s-H1-text-style {
  @include s-H1-text-style();
}

.s-H2-text-style {
  @include s-H2-text-style();
}

.s-H3-text-style {
  @include s-H3-text-style();
}

.s-H4-text-style {
  @include s-H4-text-style();
}

.s-H5-text-style {
  @include s-H5-text-style();
}

.s-H6-text-style {
  @include s-H6-text-style();
}

.s-subtitle1-text-style {
  @include s-subtitle1-text-style();
}

.s-subtitle2-text-style {
  @include s-subtitle2-text-style();
}

.s-body2-text-style {
  @include s-body2-text-style();
}

.s-body1-text-style {
  @include s-body1-text-style();
}

.s-caption-text-style {
  @include s-caption-text-style();
}

.s-overline-text-style {
  @include s-overline-text-style();
  color: s-color(neutral, 600);
}

.s-button-text-style {
  @include s-button-text-style();
}

.s-link-text-style {
  @include s-link-text-style();
}


/////////////////////////////////////////////////////////////////////////////
/// Connecting the Typographic styles defined above to Type-HTML elements
/////////////////////////////////////////////////////////////////////////////

h1 {
  margin-top: 2rem;
  margin-bottom: 1rem;
  @include s-H1-text-style();
}

h2 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  @include s-H2-text-style();
}

h3 {
  margin-top: 1.5rem;
  margin-bottom: 1rem;
  @include s-H3-text-style();
}

h4 {
  margin-top: 1rem;
  margin-bottom: 1rem;
  @include s-H4-text-style();
}

h5 {
  margin-top: 1rem;
  margin-bottom: .5rem;
  @include s-H5-text-style();
}

h6 {
  margin-top: 1rem;
  margin-bottom: .5rem;
  @include s-H6-text-style();
}

p {
  margin-top: 1rem;
  margin-bottom: 1rem;
  @include s-body1-text-style();
}

// p+p {
//   text-indent: 1rem;
// }

ul, ol {
  margin-top: .75rem;
  margin-bottom: .75rem;
  @include s-list-text-style();
}

a {
  margin-top: 1rem;
  margin-bottom: 1rem;
  text-decoration: none;
  color: s-color(primary, 500);
  @include s-link-text-style();
  transition: all 0.25s ease-in-out;
  &:hover {
    color: s-color(primary, 400);
  }
}

button {
  @include s-button-text-style;
  cursor: pointer;

  &:focus {
    outline: 0;
  }
}

code {
  font-size: $s-theme-base-font-size;
}

pre {
  margin: 2.5rem 0;
}
