body.qq-open .sidebar-qq {
  width: 100vw; }

.sidebar-omart-logo {
  margin-top: 2em;
  max-width: 100%; }

.sidebar-qq {
  height: 83px;
  z-index: 4;
  right: 0px;
  text-align: right;
  position: absolute;
  width: 80%; }
  @media (max-width: 768px) {
    .sidebar-qq {
      height: 48px;
      width: 80%;
      margin-top: -28px; } }
  .sidebar-qq .s-btn-primary {
    position: fixed;
    z-index: 10;
    right: 180px;
    top: 15px; }
    @media (max-width: 768px) {
      .sidebar-qq .s-btn-primary {
        transform: scale(0.8);
        right: 130px;
        top: 0px;
        display: none; } }
  .sidebar-qq .visible-not-at-top .s-btn {
    display: block !important; }
  .sidebar-qq .s-btn-ghost {
    position: fixed;
    right: 18px;
    z-index: 10;
    top: 15px; }
    @media (max-width: 768px) {
      .sidebar-qq .s-btn-ghost {
        transform: scale(0.8);
        right: 0px;
        top: 0px; } }
  .sidebar-qq .bm-menu-wrap {
    top: 0px; }
    @media (max-width: 768px) {
      .sidebar-qq .bm-menu-wrap {
        width: 100vw !important; } }
  .sidebar-qq .bm-overlay {
    top: 0px; }
    @media (max-width: 768px) {
      .sidebar-qq .bm-overlay {
        background-color: #fff !important; } }

.sidebar-qq > div {
  width: 100vw; }

.bm-menu {
  text-align: left; }
  .bm-menu h4 {
    margin-top: 0px; }

/* Individual item */
.bm-item {
  display: inline-block;
  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #2F3946;
  transition: color 0.2s;
  font-size: 16px; }
  .bm-item ul {
    padding-top: 1rem;
    padding-left: 1rem !important; }
    .bm-item ul li {
      padding-bottom: 1rem; }

a.bm-item {
  color: #697489; }

/* Change color on hover */
a.bm-item:hover {
  color: #1287BF; }

/* The rest copied directly from react-burger-menu docs */
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 1.5rem;
  top: 26px;
  z-index: 3 !important;
  transition-delay: 0s;
  transition: all 0.25s ease-in-out; }
  .bm-burger-button:hover .bm-burger-bars {
    background-color: #0097D4;
    opacity: 1 !important; }
  @media (max-width: 768px) {
    .bm-burger-button {
      top: 10px;
      left: 11px; } }

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background-color: #2597d0;
  transition: all 0.25s ease-in-out; }

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 50px;
  width: 50px; }

/* Color/shape of close button cross */
.bm-cross {
  background: #2597D0;
  height: 20px; }

/* General sidebar styles */
.bm-menu {
  background: white;
  padding: 0px 0px 2rem 0px;
  font-size: 1.15em; }
  @media (max-width: 768px) {
    .bm-menu {
      padding: 0px 1rem 1rem 1rem; } }

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: white; }

/* Wrapper for item list */
.bm-item-list {
  color: #2F3946; }

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.7) !important; }
