@import '../../../styles/mixins.scss';

.s-input-search-container {
  display: flex;
  flex-direction: column;
  margin: s-space(base);
  position: relative;

  .s-input-field {
    box-sizing: border-box;
    font-family: $s-theme-font-family;
    border-radius: 3px;
    border: 1px s-color(neutral, 300) solid;
    height: 40px;
    padding: s-space(extra-tight) s-space(tight);
    padding-left: 36px;


    &:focus {
      outline: s-color(primary, 400) 1px solid;
    }

    &:focus + .s-search-input-icon {
      color: s-color(primary, 400);
    }

    &::placeholder {
      color: s-color(neutral, 300);
    }

    &::-webkit-search-cancel-button {
      -webkit-appearance:searchfield-cancel-button;
    }
  }

  .s-search-input-icon {
    position: absolute;
    top: 10px;
    left: s-space(tight);
    color: s-color(neutral, 300);
  }
}