@import '../../../styles/mixins.scss';

.s-avatar {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  object-fit: cover;
  box-shadow: s-depth(100);
}

.s-avatar-small {
  height: 32px;
  width: 32px;
}

.s-avatar-medium {
  height: 64px;
  width: 64px;
}

.s-avatar-large {
  height: 104px;
  width: 104px;
}