.panel-getquote {
  position: fixed;
  width: 80vw;
  max-width: 600px;
  right: 0px;
  height: 100vh;
  z-index: 3;
  background-color: #fff;
  padding: 2rem;
  transform: translateX(100vw);
  transition: all 0.3s ease; }
  .panel-getquote.panel-open {
    transform: translateX(0vw); }

.omart-logo-header {
  height: 46px;
  margin-left: 1.5rem;
  padding-left: 1.5rem;
  border-left: 1px solid #EAEBEC; }
  @media (max-width: 768px) {
    .omart-logo-header {
      display: none; } }

.panel-callme {
  position: fixed;
  width: 500px;
  right: 0px;
  height: 100vh;
  z-index: 4;
  background-color: #fff;
  transform: translateX(100vw);
  transition: all 0.3s ease;
  padding: 0.5rem 2rem 2rem 2rem; }
  .panel-callme.panel-open {
    transform: translateX(0vw); }

.overlay {
  position: fixed;
  z-index: -1;
  opacity: 0;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  transition: opacity 0.3s ease;
  background: rgba(0, 0, 0, 0.4) !important; }
  .overlay.panel-open {
    z-index: 3;
    opacity: 1; }

nav#site-menu {
  height: 83px;
  position: fixed;
  background: -moz-linear-gradient(top, white 50%, rgba(255, 255, 255, 0.95) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, white 50%, rgba(255, 255, 255, 0.95) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, white 50%, rgba(255, 255, 255, 0.95) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#e6ffffff',GradientType=0 );
  /* IE6-9 */
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #E7E7E8;
  z-index: 2;
  top: 0px;
  position: fixed; }
  @media (max-width: 768px) {
    nav#site-menu {
      height: 50px;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end; } }
  @media (max-width: 768px) {
    nav#site-menu .homelink {
      display: none; } }
  nav#site-menu button {
    margin-right: 1.5rem; }
    @media (max-width: 768px) {
      nav#site-menu button {
        margin-right: 0.5rem;
        margin-left: 0px;
        height: 2.5rem; } }
  nav#site-menu a {
    margin-left: 84px; }
  nav#site-menu .logo {
    height: 47px;
    margin-top: 5px; }
    @media (max-width: 768px) {
      nav#site-menu .logo {
        display: none; } }
