@import '../../../styles/mixins.scss';

.s-switch-container {
  max-width: 52px;

  .s-switch-nob {
    cursor: pointer;
    height: 31px;
    width: 49px;
    background: s-color(neutral, 200);
    border-radius: 15px;
    border: 1px solid s-color(neutral, 400);
    box-sizing: border-box;
    position: relative;

    &:before {
      content: '';
      cursor: pointer;
      height: 27px;
      width: 27px;
      border-radius: 50%;
      background: s-color(neutral, 000);
      border: 1px solid s-color(neutral, 400);
      box-sizing: border-box;
      left: 1px;
      top: 1px;
      box-shadow: s-depth(100);
      position: absolute;
      transition: all s-duration(base) s-easing(base);
      max-width: 30px;
    }
  }

  input[type=checkbox]:checked + .s-switch-nob:before {
    top: 2px;
    left: 20px;
  }

  input[type=checkbox]:checked + .s-switch-nob {
    border: 0;
    background: s-color(primary, 400);

  }

  input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden;
  }
}